html {
  --blue-color: #00acdc;
  --link-height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav_list {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  margin-right: 20px;
}

.nav_item:last-child {
  margin-right: 0;
}

.nav_link {
  height: var(--link-height);
  color: #000;
  text-transform: uppercase;
  text-align: center;
  line-height: var(--link-height);
  align-items: flex-end;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
}

.is-active, .nav_link:hover {
  color: var(--blue-color);
}
